<template>
	<div
		class="theme-background-secondary"
		style="height: 100%; overflow-y: auto"
	>
		<!-- Sidebar Top -->
		<div class="text-center py-4">
			<div class="py-3">
				<h4>{{ $t("sidebar_title") }}</h4>
			</div>
			<div class="mx-auto" style="width: 135px">
				<button
					class="py-2 theme-button theme-background-light"
					@click="CreateProduct"
				>
					{{ $t("sidebar_create_product") }}
				</button>
			</div>
		</div>

		<!-- Sidebar Content -->
		<div class="sidebar-content" style="height: 400px; overflow-y: auto">
			<div v-for="(sidebar_link, index) in sidebar_links" :key="index">
				<!-- Top Level Sidebar Links-->
				<router-link
					class="d-block px-2 py-3"
					:class="
						isName(sidebar_link.name)
							? 'sidebar-link-active'
							: 'sidebar-link'
					"
					:to="sidebar_link.path"
					@click="emitClick(sidebar_link)"
				>
					<div class="d-flex justify-content-between">
						<!-- Left Sidebar Link -->
						<div>
							<span class="mx-2"
								><i :class="sidebar_link.icon"></i
							></span>
							{{ getReference(sidebar_link.title) }}
						</div>

						<!-- Right Sidebar Link -->
						<div v-if="sidebar_link.sublink">
							<i
								class="fa"
								:class="
									isNameOrChild(
										sidebar_link.name,
										sidebar_link.sublink
									)
										? 'fa-chevron-down'
										: 'fa-chevron-right'
								"
								aria-hidden="true"
							></i>
						</div>
					</div>
				</router-link>

				<!-- Sidebar SubLinks-->
				<div
					v-if="
						sidebar_link.sublink &&
						isNameOrChild(sidebar_link.name, sidebar_link.sublink)
					"
					class="mx-4"
				>
					<div
						v-for="(
							sublink_link, sublink_index
						) in sidebar_link.sublink"
						:key="sublink_index"
					>
						<router-link
							class="d-block px-2 py-3"
							:class="
								isName(sublink_link.name)
									? 'sidebar-link-active'
									: 'sidebar-link'
							"
							:to="sublink_link.path"
							@click="emitClick(sublink_link)"
						>
							{{ getReference(sublink_link.title) }}
						</router-link>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "Sidebar",
	data() {
		return {
			sidebar_links: [
				{
					icon: "fas fa-chart-line",
					title: "sidebar_dashboard_link",
					name: "Home",
					path: "/",
				},
				{
					icon: "fas fa-warehouse",
					title: "sidebar_inventory_link",
					name: "Inventory",
					path: "/inventory",
					sublink: [
						{
							title: "sidebar_inventory_create_product_link",
							name: "CreateProduct",
							path: "/inventory/create",
						},
						{
							title: "sidebar_inventory_import_product_link",
							name: "ImportProducts",
							path: "/inventory/import",
						},
					],
				},
				{
					icon: "fas fa-shopping-cart",
					title: "sidebar_orders",
					name: "Orders",
					path: "/orders",
				},
				{
					icon: "fas fa-folder-open",
					title: "sidebar_category",
					name: "Category",
					path: "/category",
				},
				{
					icon: "fas fa-truck",
					title: "sidebar_drivers",
					name: "Drivers",
					path: "/drivers",
				},
				{
					icon: "fas fa-cog",
					title: "sidebar_settings",
					name: "Settings",
					path: "/settings",
				},
				{
					icon: "fas fa-hands-helping",
					title: "sidebar_help",
					name: "Help",
					path: "/help",
				},
				{
					icon: "fas fa-bell",
					title: "sidebar_notification",
					name: "Notification",
					path: "/notify",
				},
			],
		};
	},

	methods: {
		isName(name) {
			if (this.$route.name == name) {
				return true;
			}
			return false;
		},
		isChildName(sublinks) {
			for (var i = 0; i < sublinks.length; i++) {
				if (this.$route.name == sublinks[i].name) {
					return true;
				}
			}
			return false;
		},
		isNameOrChild(name, sublinks) {
			return this.isName(name) || this.isChildName(sublinks);
		},

		CreateProduct() {
			this.$router.push("/inventory/create");
		},

		getReference(value) {
			return this.$t(value);
		},
		emitClick(link) {
			this.$emit("sidebar_click", link);
		},
	},
};
</script>

<style scoped>
/* TODO: Move these into the theme */
.sidebar-link {
	text-decoration: none;
	color: black;
	cursor: pointer;
}

.sidebar-link-active {
	text-decoration: none;
	color: white;
	background-color: var(--theme-grey);
}

.sidebar-link:hover {
	background-color: #eee;
}

/* Custom Scrollbar */

.sidebar-content::-webkit-scrollbar {
	width: 20px;
}

.sidebar-content::-webkit-scrollbar-track {
	background-color: #ddd;
}
.sidebar-content::-webkit-scrollbar-thumb {
	border: 5px solid transparent;
	border-radius: 100px;
	background-color: #999;
	background-clip: content-box;
}
.sidebar-content::-webkit-scrollbar-thumb:hover {
	background-color: #bbb;
}
</style>