<template>
	<div class="h-100">
		<!-- Navbar -->
		<Navbar />
		<!-- Responsive Sidebar Toggler -->
		<div
			class="d-md-none d-block p-3 text"
			style="cursor: pointer"
			@click="toggleSidebar"
		>
			<i class="fa fa-bars" aria-hidden="true"></i>
		</div>

		<div class="pl-0 pr-0 m-0 container-fluid">
			<div class="row h-100">
				<!-- Sidebar -->
				<div
					class="col-lg-2 col-md-3 p-0 d-md-block"
					:class="sidebar_display ? '' : 'd-none'"
				>
					<Sidebar @sidebar_click="toggleSidebar" />
				</div>

				<!-- Main Content -->
				<div
					class="container col-lg-10 col-md-9 border p-0 d-md-block"
					:class="sidebar_display ? 'd-none' : ''"
					style="overflow-y: hidden"
				>
					<!-- Dashboard subroutes-->
					<router-view v-slot="{ Component }">
						<transition name="route" mode="out-in">
							<component :is="Component" />
						</transition>
					</router-view>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
// @ is an alias to /src
import Navbar from "@/components/Navbar";
import Sidebar from "@/components/Sidebar";

export default {
	name: "Home",
	components: {
		Navbar,
		Sidebar,
	},
	data() {
		return {
			sidebar_display: false,
		};
	},
	methods: {
		toggleSidebar() {
			this.sidebar_display = !this.sidebar_display;
		},
	},
};
</script>

<style>
/* Route transitions */
.route-enter-from {
	opacity: 0;
	transform: translateX(100px);
}
.route-enter-active {
	transition: all 0.3s ease;
}
.route-leave-to {
	opacity: 0;
	transform: translateX(-100px);
}
.route-leave-active {
	transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
</style>
