<template>
	<nav
		class="
			theme-background-primary
			navbar navbar-expand
			justify-content-between
			shadow-sm
			navbar-dark
			px-5
			py-3
		"
	>
		<router-link class="brand-text navbar-brand" to="/">
			{{ $t("navbar_title") }}
		</router-link>
		<ul class="nav navbar-nav">
			<li class="nav-item active">
				<a class="nav-link" style="cursor: pointer" @click="logout">
					{{ $t("navbar_logout") }}
				</a>
			</li>
		</ul>
	</nav>
</template>

<script>
export default {
	name: "Navbar",
	methods: {
		logout() {
			this.$store.dispatch("auth/logout");
			this.$router.push("/login");
		},
	},
};
</script>

<style>
.brand-text {
	font-weight: bold;
}
</style>